@import 'vars';
@import 'mixins_n_extends';

#root_lightbox {
    @extend .all_zero;
	background-color: rgba(0, 0, 0, 0.6);
	position: fixed;
	z-index: 11;
	overflow-y: auto;
	overflow-x: hidden;
	-webkit-transform: translateZ(0);
	transform: translateZ(0);
    box-sizing: border-box;

    #close_lightbox {
        @extend .all_zero;
        position: absolute;
        cursor: pointer;
        z-index: 10;
    }

        
    #lighbox {
        z-index: 11;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-right: -50%;
        -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
        transform: translate(-50%,-50%);
        width: 320px;
        
        @media (max-height: 800px) {
            top: 30px;
            -webkit-transform: translate(-50%, 0);
            -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
        }
        
        @media (max-width: 800px) {
            top: 10px;
            -webkit-transform: translate(-50%, 0);
            -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
        }

        @include mobile {
            width: 100%;
            top: 0;
        }
        
        #lightbox_wrap {

            @include mobile {
                padding: 10px;
            }

            #lightbox_header {
                width: 100%;
                height: 54px;
                background: $white;
                border-radius: 4px 4px 0 0;
                display: table;
                box-sizing: border-box;
                color: $medium_black;
                
                #lightbox_header_text {
                    @extend .table_cell;
                    font-weight: $bold;
                    font-size: 16px;
                    padding: 15px 0 15px 20px;
                    box-sizing: border-box;
                }

                #lightbox_close {
                    width: 54px;
                    height: 100%;
                    display: table;
                    float: right;
                    cursor: pointer;
                    border-radius: 0 4px 0 0;

                    &:hover {
                        background: $light_gray;
                    }

                    .sprite {
                        margin: 0 auto;
                    }

                }
            }

            #lightbox_content {
                background: $light_gray;
                font-size: 14px;
                border-radius: 0 0 4px 4px;
                @media (max-height: 800px) {
                    margin-bottom: 30px;
                }

                .lightbox_section {
                    padding: 20px;
                    
                    /* SOCIAL AREA */
                    
                    .social_button {
                        height: 40px;
                        color: $white;
                        width: 100%;
                        border-radius: 2px;
                        cursor: pointer;

                        &:not(:last-child) {
                            margin-bottom: 10px;
                        }

                        .social_icon {
                            height: 40px;
                            width: 30px;
                            display: table;
                            border-radius: 4px 0 0 4px;
                            position: absolute;

                            .sprite {
                                margin: 0 auto;
                            }
                        }
                        .social_text {
                            padding-left: 30px;
                            box-sizing: border-box;
                            width: 100%;
                            display: table;

                            .table_cell {
                                width: 100%;
                                height: 40px;
                                border-radius: 0 4px 4px 0;
                                padding-left: 10px;
                            }
                        }
                    }

                    #facebook {
                        .social_text .table_cell {
                            background: $facebook_medium;
                        }
                        .social_icon {
                            background: $facebook_dark;
                        }
                    }

                    #facebook:hover {
                        .social_text .table_cell {
                            background: $facebook_light;
                            @include tablet {
                                background: $facebook_medium;
                            }
                        }
                        .social_icon {
                            background: $facebook_medium;
                            @include tablet {
                                background: $facebook_dark;
                            }
                        }
                    }
                    
                    #google {
                        .social_text .table_cell {
                            background: $google_medium;
                        }
                        .social_icon {
                            background: $google_dark;
                        }
                    }

                    #google:hover {
                        .social_text .table_cell {
                            background: $google_light;
                            @include tablet {
                                background: $google_medium;
                            }
                        }
                        .social_icon {
                            background: $google_medium;
                            @include tablet {
                                background: $google_dark;
                            }
                        }
                    }

                    
                    #id_card {
                        .social_text .table_cell {
                            background: $id_medium;
                            color: $medium_black;
                        }
                        .social_icon {
                            background: $id_dark;
                        }
                    }

                    #id_card:hover {
                        .social_text .table_cell {
                            background: $id_light;
                            @include tablet {
                                background: $id_medium;
                            }
                        }
                        .social_icon {
                            background: $id_medium;
                            @include tablet {
                                background: $id_dark;
                            }
                        }
                    }

                    /* INPUT AREA */

                    input {
                        margin-bottom: 10px;
                        width: 100%;
                        box-sizing: border-box;
                        border: solid 1px $lightest_gray;
                        border-radius: 4px;
                        height: 30px;
                        font-size: 13px;
                        padding: 0 10px;
                        &:focus {
                            border: 1px solid $sky_blue;
                            outline: none;
                        }
                    }
                    
                    button {
                        border-radius: 4px;
                        width: 100%;
                        outline: none;
                        border: 2px solid rgba(255, 255, 255, 0);
                        margin-bottom: 10px;
                        &:focus {
                            border: 2px solid $sky_blue;
                            border-radius: 4px;
                            outline: none;
                        }
                    }

                    .link_section {
                        text-align: center;
                        padding-bottom: 1px;

                        .lightbox_link {
                            color: $deep_sky_blue;
                            font-size: 13px;
                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }


                }



            }

        }
    }
}

